body {
   margin: 0;
   background-color: slategray;
}
.App {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   height: 100vh;
}
#header {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   border: 1px solid darkgray;
   border-radius: 10px;
   width: 350px;
   height: 460px;
   background-color: rgb(228, 228, 228);
   padding: 0;
   padding-top: 25px;
   padding-bottom: 2px;
}
.outputs {
   display: flex;
   flex-grow: 1;
   flex-wrap: wrap;
   width: 90%;
   margin-left: 25px;
}
 .outputs p {
   margin-right: 0;
   float: right;
} 
.buttons {
   display: flex;
   justify-content: center;
   background-color: rgb(240, 240, 240);
   border-radius: 5px;
   padding: 2px;
   padding-top: 15px;
   padding-bottom: 10px;
}
.number-buttons {
   flex: 0 0 73.25%;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: row-reverse;
   flex-grow: 0;
   flex-wrap: wrap;
}
.number {
   flex: 0 0 31%;
   background-color: rgb(226, 226, 226);
}
.operator-buttons {
   flex: 0 0 24.75%;
   display: flex;
   flex-wrap: wrap;
   width: 50px;
}
.operator {
   flex: 0 0 90%;
}
.number, .operator {
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: 50px;
   border-color: transparent;
   width: 45px;
   height: 60px;
   margin: 2px;
}
.operator, #module {
   background-color: rgb(193, 224, 243);
}
#zero {
   display: flex;
   flex-grow: 1;
}
#clear {
   background-color: chocolate;
   color: aliceblue;
}
.input, #display {
   flex: 0 0 100%;
}
#display {
   margin: 0;
}
.input {
   margin: 0;
   padding-top: 3px;
   padding-bottom: 3px;
   overflow: hidden;
   background-color: rgba(211, 211, 211, 0.692);
   border-radius: 5px;
   border-color: 1px solid rgb(136, 136, 136);
}
.input p {
   display: flex;
   margin-top: 4px;
   font-size: 35px;
   color: rgb(27, 27, 27);
   margin-right: 5px;
}
@keyframes cursor-blink {
   0% {
      opacity: 0;
   }
}
.input p::after {
   content: '';
   width: 2px;
   height: 42px;
   background: black;
   display: inline-block;
   animation: cursor-blink 1.5s steps(2) infinite;
}
#display p {
   font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
   font-size: x-large;
   color: midnightblue;
   padding-right: 10px;
}
#equals {
   background-color: rgba(0, 255, 128, 0.61);
}
p {
   font-size: 25px;
   font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}